import './style.css';
import { Link } from 'react-router-dom';
import {
	Friends,
	FriendsActive,
	Gaming,
	Home,
	HomeActive,
	Logo,
	Market,
	Menu,
	Messenger,
	Notifications,
	Search,
	Watch,
} from '../../svg';
import { useSelector, useDispatch } from 'react-redux';
import SearchMenu from './SearchMenu';
import { useRef, useState } from 'react';
import AllMenu from './AllMenu';
import useClickOutside from '../../helpers/clickOutside';
import UserMenu from './userMenu';
import DarkMode from '../../svg/darkMode';
import Cookies from 'js-cookie';
import Tooltip from '../toolTip';

export default function Header({ page, getAllPosts }) {
	const { user } = useSelector((user) => ({ ...user }));
	const dispatch = useDispatch();
	const { darkTheme } = useSelector((state) => ({ ...state }));
	const color = '#65676b';
	const [showSearchMenu, setShowSearchMenu] = useState(false);
	const [showAllMenu, setShowAllMenu] = useState(false);
	const [showUserMenu, setShowUserMenu] = useState(false);
	const [showMessage, setShowMessage] = useState(false);
	const [showNotification, setShowNotification] = useState(false);
	const [showMode, setShowMode] = useState(false);

	const allMenu = useRef(null);
	const userMenu = useRef(null);
	const messMenu = useRef(null);
	const notiMenu = useRef(null);

	useClickOutside(allMenu, () => {
		setShowAllMenu(false);
	});
	useClickOutside(userMenu, () => {
		setShowUserMenu(false);
	});
	useClickOutside(messMenu, () => {
		setShowMessage(false);
	});
	useClickOutside(notiMenu, () => {
		setShowNotification(false);
	});

	const setDarkMode = () => {
		if (Cookies.get('darkTheme') === 'true' || null) {
			Cookies.set('darkTheme', false);
			dispatch({ type: 'LIGHT' });
		} else {
			Cookies.set('darkTheme', true);
			dispatch({ type: 'DARK' });
		}
	};

	return (
		<header>
			<div className="header_left">
				<Link to="/" className="header_logo">
					<div className="circle">
						<Logo />
					</div>
				</Link>
				<div
					className="search search1"
					onClick={() => {
						setShowSearchMenu(true);
					}}
				>
					<Search color={color} />
					<input type="text" placeholder="Tìm kiếm" className="hide_input" />
				</div>
			</div>
			{showSearchMenu && <SearchMenu color={color} setShowSearchMenu={setShowSearchMenu} token={user.token} />}
			<div className="header_middle">
				<Link
					to="/"
					className={`middle_icon ${page === 'home' ? 'active' : 'hover1'} button_animation`}
					// onClick={() => getAllPosts()}
				>
					{page === 'home' ? <HomeActive /> : <Home color={color} />}
				</Link>
				<Link
					to="/friends"
					className={`middle_icon ${page === 'friends' ? 'active' : 'hover1'} button_animation`}
				>
					{page === 'friends' ? <FriendsActive /> : <Friends color={color} />}
				</Link>
				<Link to="/" className="middle_icon hover1 button_animation">
					<Watch color={color} />
					<div className="middle_notification">9+</div>
				</Link>
				<Link to="/" className="middle_icon hover1 button_animation">
					<Market color={color} />
				</Link>
				<Link to="/" className="middle_icon hover1 button_animation">
					<Gaming color={color} />
				</Link>
			</div>
			<div className="header_right">
				<Tooltip content="Khám phá">
					<div
						className={`circle_icon hover1 button_animation ${showAllMenu && 'active_header'}`}
						ref={allMenu}
					>
						<div
							onClick={() => {
								setShowAllMenu((prev) => !prev);
							}}
							style={{ transform: 'translateY(2px)' }}
						>
							<Menu />
						</div>

						{showAllMenu && <AllMenu />}
					</div>
				</Tooltip>
				<Tooltip content="Tin nhắn">
					<div
						onClick={() => {
							setShowMessage((prev) => !prev);
						}}
						className={`circle_icon hover1 button_animation ${showMessage && 'active_header'}`}
						ref={messMenu}
					>
						<Messenger />
					</div>
				</Tooltip>
				<Tooltip content="Thông báo">
					<div
						onClick={() => {
							setShowNotification((prev) => !prev);
						}}
						className={`circle_icon hover1 button_animation ${showNotification && 'active_header'}`}
						ref={notiMenu}
					>
						<Notifications />

						<div className="right_notification">5</div>
					</div>
				</Tooltip>
				<Tooltip content="Giao diện">
					<div
						className={`circle_icon hover1 button_animation ${showMode && 'active_header'}`}
						onClick={setDarkMode}
					>
						<DarkMode />
					</div>
				</Tooltip>
				<div className={`circle_icon hover1 ${showUserMenu && 'active_header'}`} ref={userMenu}>
					<div
						onClick={() => {
							setShowUserMenu((prev) => !prev);
						}}
					>
						<div style={{ transform: 'translateX(4px)' }}>
							<img
								className={`circle_icon hover1 ${showUserMenu && 'active_header'} button_animation`}
								src={user?.picture}
								alt=""
							/>
						</div>
					</div>

					{showUserMenu && <UserMenu user={user} />}
				</div>
			</div>
		</header>
	);
}
