export default function Bio({
	infos,
	handleChange,
	max,
	setShowBio,
	updateDetails,
	placeholder,
	name,
	detail,
	setShow,
	rel,
}) {
	return (
		<div className="add_bio_wrap">
			{rel ? (
				<select className="select_rel" name={name} value={infos.relationship} onChange={handleChange}>
					<option value="Độc thân">Độc thân</option>
					<option value="Đang hẹn hò">Đang hẹn hò</option>
					<option value="Đã kết hôn">Đã kết hôn</option>
					<option value="Đã ly hôn">Đã ly hôn</option>
					<option value="Quan hệ phức tạp">Quan hệ phức tạp</option>
				</select>
			) : (
				<textarea
					placeholder={placeholder}
					name={name}
					value={infos?.[name]}
					maxLength={detail ? 25 : 100}
					className="textarea_blue details_input"
					onChange={handleChange}
				></textarea>
			)}
			{!detail && <div className="remaining">{max} ký tự còn lại</div>}
			<div className="flex">
				<div className="flex flex_right">
					<button className="gray_btn" onClick={() => (!detail ? setShowBio(false) : setShow(false))}>
						Hủy
					</button>
					<button
						className="blue_btn"
						onClick={() => {
							updateDetails();
							setShow(false);
						}}
					>
						Lưu
					</button>
				</div>
			</div>
		</div>
	);
}
