import { useRef } from 'react';
import Detail from './Detail';
import useOnCLickOutside from '../../helpers/clickOutside';

export default function EditDetails({ details, handleChange, updateDetails, infos, setVisible }) {
	const modal = useRef(null);
	useOnCLickOutside(modal, () => setVisible(false));
	return (
		<div className="blur">
			<div className="postBox infosBox" ref={modal}>
				<div className="box_header">
					<div className="small_circle" onClick={() => setVisible(false)}>
						<i className="exit_icon"></i>
					</div>
					<span>Chỉnh sửa chi tiết</span>
				</div>
				<div className="details_wrapper scrollbar">
					<div className="details_col">
						<span>Chỉnh sửa giới thiệu bản thân</span>
						<span>Chi tiết thông tin bạn muốn công khai</span>
					</div>
					<div className="details_header">Tến khác</div>
					<Detail
						value={details?.otherName}
						img="studies"
						placeholder="Thêm tên khác"
						name="otherName"
						text="tên khác"
						handleChange={handleChange}
						updateDetails={updateDetails}
						infos={infos}
					/>
					<div className="details_header">Công việc</div>
					<Detail
						value={details?.job}
						img="job"
						placeholder="Thêm công việc"
						name="job"
						text="công việc"
						handleChange={handleChange}
						updateDetails={updateDetails}
						infos={infos}
					/>
					<Detail
						value={details?.workplace}
						img="job"
						placeholder="Thêm nơi làm việc"
						name="workplace"
						text="nơi làm việc"
						handleChange={handleChange}
						updateDetails={updateDetails}
						infos={infos}
					/>
					<div className="details_header">Giáo dục</div>
					<Detail
						value={details?.highSchool}
						img="studies"
						placeholder="Thêm trường THPT"
						name="highSchool"
						text="trường THPT"
						handleChange={handleChange}
						updateDetails={updateDetails}
						infos={infos}
					/>
					<Detail
						value={details?.college}
						img="studies"
						placeholder="Thêm trường Đại học/Cao đẳng"
						name="college"
						text="trường Đại học/ Cao đẳng"
						handleChange={handleChange}
						updateDetails={updateDetails}
						infos={infos}
					/>
					<div className="details_header">Thành phố hiện nay</div>
					<Detail
						value={details?.currentCity}
						img="home"
						placeholder="Thêm thành phố hiện nay"
						name="currentCity"
						text="thành phố hiện nay"
						handleChange={handleChange}
						updateDetails={updateDetails}
						infos={infos}
					/>
					<div className="details_header">Quê quán</div>
					<Detail
						value={details?.hometown}
						img="home"
						placeholder="Thêm quê quán"
						name="hometown"
						text="quê quán"
						handleChange={handleChange}
						updateDetails={updateDetails}
						infos={infos}
					/>
					<div className="details_header">Tình trạng hôn nhân</div>
					<Detail
						value={details?.relationship}
						img="relationship"
						placeholder="Thêm tình trạng hôn nhân"
						name="relationship"
						text="tình trạng hôn nhân"
						handleChange={handleChange}
						updateDetails={updateDetails}
						infos={infos}
						rel
					/>
					<div className="details_header">Zalo</div>
					<Detail
						value={details?.zalo}
						img="home"
						placeholder="Thêm Số điện thoại Zalo: vd: 0987xxxxxx"
						name="zalo"
						text="zalo"
						handleChange={handleChange}
						updateDetails={updateDetails}
						infos={infos}
					/>
				</div>
			</div>
		</div>
	);
}
